<template>
  <div class="manage-photos">
    <div class="manage-photos-header">
      <PublicHead title="管理照片" :isBtn="false" />
    </div>
    <div class="view-method">
      <div class="view-name">
        按上传时间
      </div>
      <div class="view-icon">
        <img src="../../assets/img/staffOnly/btn_sorting.png" alt="">
      </div>
    </div>
    <div class="photos">
      <div class="photos-item" v-for="item in photosList" :key="item._id">
        <div class="photos-img">
          <img :src="item.url" alt="">
        </div>
        <div class="photos-right">
          <div class="photos-text">
            <div class="photos-name ellipse">
              {{item.name}}
            </div>
            <div class="photos-time">
              {{item.time}}
            </div>
            <div class="delete-photos">
              <img src="../../assets/img/staffOnly/btn_delete.png" alt="">
              <span>删除</span>
            </div>
          </div>
          <div :class="[item.show == 1?'photos-show' : 'photos-hide']" @click="setImgShow(item)">
            <div class="photos-switch"></div>
            <div class="photos-eye"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer-box"></div>
    <div class="footer">
      <div class="upload-photos-btn">
        上传照片
      </div>
    </div>
  </div>
</template>

<script>
import PublicHead from '../../components/publicHead.vue'
export default {
  data(){
    return {
      pageIndex: 1,  //当前页
      pageSize: 15,  //一页多少条   
      openid: '123456',  //用户id

      photosList: [],  //图片列表
    }
  },
  methods: {
    async getResource(){
      try {
        let res = await this.$api.staffOnly.getResource({
          pageIndex: this.pageIndex,
          pageSize: this.pageSize,
          openid: this.openid
        })

        console.log(res);
        if(res.statusCode !== 75200){
          return;
        }
        this.photosList = res.list
        
      } catch (err) {
        console.log(err);
        this.$zero.Toast('服务器异常，请稍后重试');
      }
    },
    async setImgShow(item){
      try {

        let res = await this.$api.staffOnly.setImgShow({
          // openid: this.openid,
          id: item._id,
          show: item.show == 1? 2 : 1
        })

        if(res.statusCode === 75200){
          item.show = item.show == 1? 2 : 1;
        }else{
          this.$zero.Toast('服务器异常，请稍后重试');
        }

      } catch (err) {
        console.log(err);
        this.$zero.Toast('服务器异常，请稍后重试');
      }
    }
  },
  created(){
    this.getResource();
  },
  components: {
    PublicHead
  }
}
</script>

<style scoped>
.manage-photos {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: url("../../assets/img/contactUs/bg_popup.png") no-repeat;
  background-size: 100% 100%;
}
.manage-photos-header {
  /* padding: 0.4rem 0.2rem 0.3rem 0.4rem; */
  padding-bottom: 0.3rem;
}
.view-method {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #337488;
  position: relative;
}
.view-name {
  height: 0.5rem;
  font-size: 0.3rem;
  font-weight: bold;
  color: #00ffcc;
  padding: 0 .15rem;
  background: url("../../assets/img/staffOnly/bg_chosen.png") no-repeat center bottom;
  background-size: 110% 80%;
}
.view-icon{
  width: .21rem;
  height: .31rem;
  margin-top: .08rem;
}
.view-icon>img{
  width: 100%;
  height: 100%;
}
.photos {
  width: 100%;
  box-sizing: border-box;
  padding: 0.2rem 0.2rem 0;
}
.photos-item {
  width: 100%;
  display: flex;
  align-items: center;
  padding-bottom: 0.2rem;
}
.photos-img {
  width: 1.61rem;
  height: 1.61rem;
}
.photos-img > img {
  width: 100%;
  height: 100%;
}
.photos-right {
  flex: 1;
  padding: 0.2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(to right,rgba(0, 179, 255, 0.25) 0%, rgba(0, 255, 213, 0.25) 100%);
}
.photos-text {
  flex: 1;
}
.photos-name {
  font-size: 0.3rem;
  color: #fff;
}
.photos-time {
  font-size: 0.24rem;
  color: #bfdae0;
  padding-top: 0.05rem;
}
.delete-photos {
  font-size: 0.24rem;
  color: #ff5555;
  padding-top: 0.18rem;
  display: flex;
  align-items: center;
}
.delete-photos>img{
  width: .25rem;
  height: .25rem;
  margin-right: .05rem;
}
.photos-show,
.photos-hide {
  display: flex;
  align-items: center;
}
.photos-switch {
  width: 0.64rem;
  height: 0.41rem;

  margin-right: 0.3rem;
}
.photos-eye {
  width: 0.31rem;
  height: 0.31rem;
}
.photos-show .photos-switch {
  background: url("../../assets/img/staffOnly/switch_on.png") no-repeat;
  background-size: 100% 100%;
}
.photos-show .photos-eye {
  background: url("../../assets/img/staffOnly/ico_show.png") no-repeat;
  background-size: 100% 100%;
}
.photos-hide .photos-switch {
  background: url("../../assets/img/staffOnly/switch_off.png") no-repeat;
  background-size: 100% 100%;
}
.photos-hide .photos-eye {
  background: url("../../assets/img/staffOnly/ico_hide.png") no-repeat;
  background-size: 100% 100%;
}
.footer-box {
  height: 1.34rem;
}
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 0.25rem 0;
  border-top: 1px solid #33758c;
  background: linear-gradient( #005270 0%, #006b78 100%);
  /* background-color: #000; */
}
.upload-photos-btn {
  width: 2.84rem;
  height: 0.84rem;
  margin: 0 auto;
  color: #fff;
  font-size: 0.28rem;
  text-align: center;
  line-height: 0.84rem;
  background: url("../../assets/img/btn_3.png") no-repeat;
  background-size: 100% 100%;
}
</style>